import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  linkActiveClass: 'active',
  linkExactActiveClass: 'active',
  routes: [
    { path: '/', name: 'home', component: Home, meta: { title: '泰璞健康', keepAlive: true } },
    { path: '/about', name: 'about', component: () => import('./views/About.vue'), meta: { title: '关于我们', keepAlive: false } },
    { path: '/server', name: 'server', component: () => import('./views/Server.vue'), meta: { title: '服务案例', keepAlive: false } },
    { path: '/hospital', name: 'hospital', component: () => import('./views/Hospital.vue'), meta: { title: '合作医院', keepAlive: false } },
    { path: '/team', name: 'team', component: () => import('./views/Team.vue'), meta: { title: '专家团队', keepAlive: false } },
    { path: '/consult', name: 'consult', component: () => import('./views/Consult.vue'), meta: { title: '咨询', keepAlive: false } },
    { path: '/experience', name: 'experience', component: () => import('./views/Experience.vue'), meta: { title: '精密体验', keepAlive: true } },
    { path: '/purify', name: 'purify', component: () => import('./views/Purify.vue'), meta: { title: '血液净化', keepAlive: true } },
    { path: '/antifading', name: 'antifading', component: () => import('./views/Antifading.vue'), meta: { title: '干细胞抗衰', keepAlive: true } },
    { path: '/hospital-detail', name: 'hospital-detail', component: () => import('./views/HospitalDetail.vue'), meta: { keepAlive: true } },
    { path: '/team-detail', name: 'team-detail', component: () => import('./views/TeamDetail.vue'), meta: { keepAlive: true } },
    { path: '/server-detail', name: 'server-detail', component: () => import('./views/ServerDetail.vue'), meta: { keepAlive: true } },
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
});

export default router
