<template>
	<div id="app">
		<keep-alive>
			<router-view v-if="$route.meta.keepAlive" @returnMenuRes="returnRes" />
		</keep-alive>
		<router-view v-if="!$route.meta.keepAlive"></router-view>
		
		<Tabbar :type="activeType" @returnMenuRes="returnRes" />
		<Menu :show="showMenu" @returnMenuRes="returnRes" />
	</div>
</template>

<script>
	import Menu from '@/views/Menu.vue'
	import Tabbar from '@/views/Tabbar.vue'
	import _ from 'lodash'

	export default {
		components: { Menu, Tabbar },
		data() {
			return {
				showMenu: false,
				activeType: '',
			}
		},
		mounted() {
			console.log(this.$route);
			if (!location.pathname.substring(1)) {
				sessionStorage.activeType = 'Home';
			}else {
				sessionStorage.activeType = _.upperFirst(location.pathname.substring(1));
				this.activeType = _.upperFirst(location.pathname.substring(1));
			}
		},
		methods: {
			returnRes(res) {
				if (res.activeType == 'Menu') {
					this.showMenu = res.showMenu;
				} else {
					this.showMenu = res.showMenu;
					this.activeType = res.activeType;
					sessionStorage.activeType = res.activeType;
				}
			}
		},
		watch: {
			'$route'(to, form) {
				this.showMenu = false;
				this.activeType = _.upperFirst(to.name);
			},
			activeType: {
				handler(newValue) {
					this.activeType = newValue;
				}
			}
		}
	}
</script>

